<template>
    <div id="singleDeck_css">
        <div id="welcomeUser">
        <div id="websiteIcon">
            <img src="../assets/MonkeyFace_smaller.png" alt="LOGO">
        </div>
        <div id="welcomeWords">
        <!-- <h1>Welcome, {{emittedUser.userName?emittedUser.userName:""}}!</h1> -->
        <h1 v-if="!editDeckNameSelected">{{emittedObject.deckName?emittedObject.deckName:""}}</h1>
        </div>
        <div id="hamburgerMenu" v-on:click="hamburgerWasClicked">
        <img src="../assets/Hamburger_icon_small.svg.png" alt="MENU">
        <div id=hamburgerDropdown v-if="hamburgerClicked">
        <button class="buttonClass" v-on:click="returnToLoginPage()">Log Out</button>
        <!-- <button class="decksReturnButton" v-on:click="goBackToDecks">Return To Decks</button> -->
        <button class="buttonClass" v-on:click="goBackToDecks">To Decks</button>
        </div>
    </div>
    </div>
        <!-- <h1 v-if="!editDeckNameSelected">{{emittedObject.deckName?emittedObject.deckName:""}}</h1> -->
        <div class ="textBox">
            <input type="text" placeholder="Type the new deck name" v-model="editDeckNameInput" v-if="editDeckNameSelected" v-focus @keyup.enter="submitEditedDeckName"/>
        </div>

        <div>
            <button class="deckEditButton" v-on:click="editDeckName">Edit Deck</button>
            <button class="deckDeleteButton" v-on:click="deleteDeckPressed">Delete Deck</button>
            <!-- <br> -->
            <button class="decksReturnButton" v-if="!deckIsShuffled" v-on:click="shuffleDeck">Shuffle</button>
            <button class="decksReturnButton" v-if="deckIsShuffled" v-on:click="unShuffleDeck">Un-Shuffle</button>
            <!-- <button class="decksReturnButton" v-on:click="goBackToDecks">Return To Decks</button> -->
            <br>
            <input type="text" placeholder="Search the deck:" v-model="deckSearchInput" @keyup.enter="deckSearch"/>
        </div>

        <p v-if="deleteDeckButtonPressed">Are you sure that you want to delete {{emittedObject.deckName?emittedObject.deckName:""}}?</p>
        <button class="decksReturnButton" v-if="deleteDeckButtonPressed" v-on:click="deleteDeck">Yes, delete the deck.</button>
        <button class="decksReturnButton" v-if="deleteDeckButtonPressed" v-on:click="doNotDeleteDeck">No, don't delete the deck.</button>

        <p v-if="deleteCardButtonPressed">Are you sure that you want to delete this card?</p>
        <button class="decksReturnButton" v-if="deleteCardButtonPressed" v-on:click="deleteCard">Yes, delete the card.</button>
        <button class="decksReturnButton" v-if="deleteCardButtonPressed" v-on:click="doNotDeleteCard">No, don't delete the card.</button>
        
        <!-- https://vuejs.org/v2/guide/class-and-style.html#With-Components how to use the v-bind-->
        <div class="card" v-bind:class="{flipped: this.cardSide==='Front'}">
            <p class="cardPromptClass1">{{cardSide}} {{this.cardsListIndex+1}}/{{emittedObject.cards?emittedObject.cards.length:""}}</p>
            <p class="cardPromptClass2" v-if="!addCardFront&&!addCardBack">{{cardPrompt}}</p>

            <input type="text" ref="frontInput" class="cardInputBox" placeholder="Type front text" v-model="cardFrontInput" v-if="addCardFront" v-focus @keyup.enter="flipCard"/>
            <input type="text" ref="backInput" class="cardInputBox" placeholder="Type back text" v-model="cardBackInput" v-if="addCardBack" v-focus @keyup.enter="submitCard"/>

            <!-- edit card -->
            <input type="text" ref="frontInput" class="cardInputBox" placeholder="Type new front text" v-model="cardFrontInput" v-if="editCardButtonPressed&&cardSide==='Front'" v-focus @keyup.enter="submitEditedCardFront"/>
            <input type="text" ref="backInput" class="cardInputBox" placeholder="Type new back text" v-model="cardBackInput" v-if="editCardButtonPressed&&cardSide==='Back'" v-focus @keyup.enter="submitEditedCardBack"/>

            <div id="cardButtonsDiv">
                <select id="selectBlock" v-if="!isMobile" v-model="selectedLanguage"> 
                    <option disabled value="">Please select a language:</option>
                    <option :value="option.name" :key="option" v-for="option in this.optionList">{{option.name}}</option>
                </select>
                <br>
                <button class="cardButton" v-on:click="readCard">Read Card Aloud</button>
                <br>
                <button class="cardNavigationButtons" id="cardNavigationButton1" v-on:click="updateCardIndex(-1)"><img src="../assets/left_arrow_small_crop.png" alt="left arrow" /></button>
                
                <button class="cardButton" v-on:click="flipCard" v-if="!addCardBack&&!editCardButtonPressed">Flip Card</button>
                <button class="cardButton" v-on:click="submitCard" v-if="addCardBack&&!editCardButtonPressed">Submit Card</button>

                <button class="cardButton" v-on:click="submitEditedCardFront" v-if="editCardFront&&editCardButtonPressed">Submit Edit</button>
                <button class="cardButton" v-on:click="submitEditedCardBack" v-if="editCardBack&&editCardButtonPressed">Submit Edit</button>

                <button class="cardNavigationButtons" id="cardNavigationButton2" v-on:click="updateCardIndex(1)"><img src="../assets/right_arrow_small_crop.png" alt="right arrow" /></button>
            </div>
        </div>
        <button class="decksReturnButton" v-if="addCardFront||addCardBack" v-on:click="abortAddCard">Abort Add Card</button>
        <button class="addCardButton" v-on:click="addCard">Add Card</button>
        <button class="deleteCardButton" v-on:click="deleteCardPressed">Delete Card</button>
        <button class="editCardButton" v-on:click="editCardPressed">Edit Card</button>
        
        <div class="snackbar" id="snackbar1">There is only one card in the deck. Please add more cards.</div>
        <div class="snackbar" id="snackbar2">There is no card to flip. Please add a card.</div>
        <div class="snackbar" id="snackbar3">There are no cards in the deck. Please add a card.</div>
        <div class="snackbar" id="snackbar4">Please enter a valid deck name.</div>
        <div class="snackbar" id="snackbar5">Any card with blank front or back will not be submitted.</div>
        <div class="snackbar" id="snackbar6">There are no cards to delete in this deck.</div>
        <div class="snackbar" id="snackbar7">No card matching the search term was found.</div>
        <div class="snackbar" id="snackbar8">There are no cards to edit in this deck.</div>
        <div class="snackbar" id="snackbar9">Please unshuffle the deck before editing or deleting cards.</div>
    </div>
</template>

<script>
import Vue from 'vue'
import axios from 'axios';
const url = '/api/decks/';

var synth = window.speechSynthesis;
var isMobile = /iPhone|iPad|iPod|Android/i.test(navigator.userAgent);

export default {
    props: {
        emittedObject: {
            type: Object,
            required: true,
            _id: {
                type: String,
                required: true
            },
            cards: {
                type: Array,
                required: true
            },
            deckName: {
                type: String,
                required: true
            }
        }
    },
    // https://vuejs.org/v2/guide/custom-directive.html
    // allows the use of v-focus to have the cursor automatically go into the textbox
    directives: {
        focus: {
            // directive definition
            inserted: function (el) {
                Vue.nextTick(() => el.focus());
            }
        }
    },

    data () {
        return {
            cardSide:"Front",
            cardPrompt:"Please add a card by clicking the 'Add Card' button below.",
            cardFrontInput:"",
            cardBackInput:"",
            addCardFront:false,
            addCardBack:false,
            editCardFront:false,
            editCardBack:false,
            cardsListIndex:0,
            editDeckNameSelected:false,
            editDeckNameInput:"",
            cardId:"",
            deleteDeckButtonPressed:false,
            deleteCardButtonPressed:false,
            editCardButtonPressed:false,
            optionList:[],
            selectedLanguage: "",
            isMobile:isMobile,
            deckSearchInput:"",
            deckIsShuffled:false,
            hamburgerClicked:false
        }
    },
    methods: {
        returnToLoginPage(){
            localStorage.removeItem("emittedUser._id");
            localStorage.removeItem("emittedObject._id");
            this.$router.push({path: `/`});
        },
        hamburgerWasClicked() {
            if (!this.hamburgerClicked){
                this.hamburgerClicked=true;
            } else {
                this.hamburgerClicked=false;
            }
        },
        async deckSearch() {
            let cardFound = false;
            let indexVar = -1;
            // Search the card fronts for the search term
            for (let i=0; i < this.emittedObject.cards.length; i++) {
                if (this.emittedObject.cards[i].cardFront===this.deckSearchInput){
                    indexVar = i;
                    cardFound=true;
                    break;
                }
            }
            // if there was a match on the card fronts, show that card front
            if (cardFound === true){
                this.cardsListIndex=indexVar;
                this.cardSide="Front"
                this.cardPrompt=this.emittedObject.cards[this.cardsListIndex].cardFront;
                this.deckSearchInput="";
                return;
            }
            // if the card still hasn't been found, check the backs of the cards
            if (cardFound === false) {
                for (let i=0; i < this.emittedObject.cards.length; i++) {
                    if (this.emittedObject.cards[i].cardBack===this.deckSearchInput){
                        indexVar = i;
                        cardFound=true;
                        break;
                    }
                }
            }
            // if there was a match on the card backs, show that card back
            if (cardFound === true){
                this.cardsListIndex=indexVar;
                this.cardSide="Back"
                this.cardPrompt=this.emittedObject.cards[this.cardsListIndex].cardBack;
                this.deckSearchInput="";
                return;
            }
            // if there was no match, show the snackbar saying that there wasn't a match
            else{
                this.deckSearchInput="";
                this.showSnackBar("snackbar7");
                return;
            }
            
        },
        populateVoiceList() {
            this.optionList = synth.getVoices();
        },
        shuffleVueArray(array) {
            for (let i = array.length - 1; i > 0; i--) {
                const j = Math.floor(Math.random() * (i + 1));
                const temp = array[i];
                Vue.set(array, i, array[j]);
                Vue.set(array, j, temp);
            }
        },  
        focusOnCardFrontInput () {
            this.$nextTick(() => {
                this.$refs.frontInput.focus();
            });
        },
        focusOnCardBackInput () {
            this.$nextTick(() => {
                this.$refs.backInput.focus();
            });
        },
        readCard () {
            const language = this.optionList.filter(item => item.name === this.selectedLanguage);
            let utterance = new SpeechSynthesisUtterance(this.cardPrompt);
            utterance.voice = language[0];
            speechSynthesis.speak(utterance);
        },
        flipCard () {
            if (this.emittedObject.cards.length === 0 && !this.addCardBack && !this.addCardFront){
                this.showSnackBar("snackbar2");
                return;
            }
            if (this.addCardFront) {
                this.cardSide="Back";
                this.addCardBack=true;
                this.addCardFront=false;
                this.focusOnCardBackInput();
                return;
            }
            if (this.cardSide==="Front") {
                this.cardSide="Back";
                this.cardPrompt=this.emittedObject.cards[this.cardsListIndex].cardBack;
            } else {
                this.cardSide="Front";
                this.cardPrompt=this.emittedObject.cards[this.cardsListIndex].cardFront;
            }   
        },
        addCard () {
            this.addCardFront=true;
        },
        abortAddCard () {
            this.addCardFront=false;
            this.addCardBack=false;
            this.cardSide="Front";
            this.cardFrontInput="";
            this.cardBackInput="";
            if (this.emittedObject.cards.length === 0) {
                    return;
            }
            else {   
                this.cardPrompt=this.emittedObject.cards[this.cardsListIndex].cardFront;
                this.cardId=this.emittedObject.cards[this.cardsListIndex]._id;
                return;
            }
        },
        abortEditCard () {
            this.editCardButtonPressed=false;
            this.editCardFront=false;
            this.editCardBack=false;
            this.cardSide="Front";
            this.cardFrontInput="";
            this.cardBackInput="";
            if (this.emittedObject.cards.length === 0) {
                    return;
            }
            else {   
                this.cardPrompt=this.emittedObject.cards[this.cardsListIndex].cardFront;
                this.cardId=this.emittedObject.cards[this.cardsListIndex]._id;
                return;
            }
        },
        async submitCard () {
            if (this.cardFrontInput == "" || this.cardBackInput == "") {
                this.abortAddCard();
                this.showSnackBar("snackbar5");
                return;
            }
            const response = await axios.post(url+this.emittedObject._id+"/cards/"+this.cardsListIndex,{cardFront:this.cardFrontInput,cardBack:this.cardBackInput});
            if(response.status!==201){
                console.log("error: ",response);
            }
            this.emittedObject.cards = response.data.cards;
            this.addCardFront=false;
            this.addCardBack=false;
            this.cardSide="Front";
            this.cardFrontInput="";
            this.cardBackInput="";
            if (this.emittedObject.cards.length - this.cardsListIndex > 1){
                this.cardsListIndex+=1;
            }
            this.cardPrompt=this.emittedObject.cards[this.cardsListIndex].cardFront;
            this.cardId=this.emittedObject.cards[this.cardsListIndex]._id;
        },
        async submitEditedCardFront () {
            if (this.cardFrontInput == "") {
                this.abortEditCard();
                this.showSnackBar("snackbar5");
                return;
            }
            const response = await axios.put(url+this.emittedObject._id+"/cards/"+"front/"+this.cardId+"/"+this.cardsListIndex,{cardFront:this.cardFrontInput,cardBack:this.emittedObject.cards[this.cardsListIndex].cardBack});
            if(response.status!==201){
                console.log("error: ",response);
            }
            this.emittedObject.cards = response.data.cards;
            this.editCardFront=false;
            this.editCardButtonPressed=false;
            this.cardFrontInput="";
            this.cardPrompt=this.emittedObject.cards[this.cardsListIndex].cardFront;
        },
        async submitEditedCardBack () {
            if (this.cardBackInput == "") {
                this.abortEditCard();
                this.showSnackBar("snackbar5");
                return;
            }
            const response = await axios.put(url+this.emittedObject._id+"/cards/"+"back/"+this.cardId+"/"+this.cardsListIndex,{cardFront:this.emittedObject.cards[this.cardsListIndex].cardFront,cardBack:this.cardBackInput});
            if(response.status!==201){
                console.log("error: ",response);
            }
            this.emittedObject.cards = response.data.cards;
            this.editCardBack=false;
            this.editCardButtonPressed=false;
            this.cardBackInput="";
            this.cardPrompt=this.emittedObject.cards[this.cardsListIndex].cardBack;
        },
        updateCardIndex (indexToAdd) {
            if (this.emittedObject.cards.length === 0){
                this.showSnackBar("snackbar3");
                return;
            }
            if (this.emittedObject.cards.length === 1){
                this.showSnackBar("snackbar1");
                return;
            }
            if (indexToAdd + this.cardsListIndex < 0) {
                this.cardsListIndex = this.emittedObject.cards.length-1;
            } else if (indexToAdd + this.cardsListIndex > this.emittedObject.cards.length-1) {
                this.cardsListIndex = 0;
            } else {
                this.cardsListIndex = indexToAdd + this.cardsListIndex;
            }
            this.cardSide="Front";
            this.cardPrompt=this.emittedObject.cards[this.cardsListIndex].cardFront;
            this.cardId=this.emittedObject.cards[this.cardsListIndex]._id;
        },
        editCardPressed () {
            if (this.deckIsShuffled){
                this.showSnackBar('snackbar9');
                return;
            }
            if (this.emittedObject.cards.length === 0) {
                this.showSnackBar("snackbar8");
                return;
            }
            else {
                this.editCardButtonPressed = true;
                if (this.cardSide==="Front"){
                    this.editCardFront=true;
                }
                if (this.cardSide==="Back"){
                    this.editCardBack=true;
                }
                return;
            }
        },
        deleteCardPressed () {
            if (this.deckIsShuffled){
                this.showSnackBar('snackbar9');
                return;
            }
            if (this.emittedObject.cards.length === 0) {
                this.showSnackBar("snackbar6");
                return;
            }
            else {
                this.deleteCardButtonPressed = true;
                return;
            }
        },
        async deleteCard () {
            this.deleteCardButtonPressed = false;
            await axios.delete(url+this.emittedObject._id+"/cards/"+this.cardId);
            this.emittedObject.cards.splice(this.cardsListIndex,1);
            
            if (this.emittedObject.cards.length-1 >= 0){
                this.cardsListIndex = this.cardsListIndex === 0 ? 0 : this.cardsListIndex - 1;
                this.cardPrompt=this.emittedObject.cards[this.cardsListIndex].cardFront;
                this.cardId=this.emittedObject.cards[this.cardsListIndex]._id;
                this.cardSide="Front";
            }
            else{
                this.cardPrompt="Please add a card by clicking the 'Add Card' button below.";
            }
        },
        async doNotDeleteCard(){
            this.deleteCardButtonPressed = false;
        },
        goBackToDecks () {
            localStorage.removeItem("emittedObject._id");
            //advance route back to the Welcome Page
            this.$router.push({ path: '/welcome' })
        },
        // because I am shuffling the emittedObject.cards array, the shuffling is only temporary. The database is not changed in any way.
        shuffleDeck () {
            if (this.emittedObject.cards.length === 0) {
                this.showSnackBar("snackbar3");
                return
            }
            if (this.emittedObject.cards.length === 1) {
                this.showSnackBar("snackbar1");
                return
            }
            this.shuffleVueArray(this.emittedObject.cards);
            this.cardPrompt=this.emittedObject.cards[this.cardsListIndex].cardFront;
            this.cardSide="Front";
            this.cardId=this.emittedObject.cards[this.cardsListIndex].cardId;
            this.deckIsShuffled=true;
        },
        async unShuffleDeck () {
            window.location.reload();
            this.deckIsShuffled=false;
        },
        deleteDeckPressed () {
            this.deleteDeckButtonPressed = true;
        },
        async deleteDeck(){
            this.deleteDeckButtonPressed = false;
            await axios.delete(url+this.emittedObject._id+"/deckName");
            this.goBackToDecks();
        },
        async doNotDeleteDeck(){
            this.deleteDeckButtonPressed = false;
        },
        editDeckName(){
            this.editDeckNameSelected=true;
        },
        async submitEditedDeckName(){
            if (this.editDeckNameInput == ""){
                this.editDeckNameSelected=false;
                this.showSnackBar("snackbar4");
                return;
            }
            this.emittedObject.deckName=this.editDeckNameInput;
            const response = await axios.put(url+this.emittedObject._id+"/deckName",{deckName:this.editDeckNameInput});
            if(response.status!==201){
                console.log("error: ",response);
            }
            this.editDeckNameInput="";
            this.editDeckNameSelected=false;
        },
        showSnackBar(snackBarNum) {
            // Get the snackbar DIV
            var x = document.getElementById(snackBarNum);
            // Add the "show" class to DIV
            x.classList.add("show");
            // After 3 seconds, remove the show class from DIV
            setTimeout(function(){ x.classList.remove("show"); }, 3000);
    }
    },
    async created () {
        this.populateVoiceList();
        if (speechSynthesis.onvoiceschanged !== undefined) {
            speechSynthesis.onvoiceschanged = this.populateVoiceList;
        }
        if (this.emittedObject._id != undefined) {
            localStorage.setItem("emittedObject._id",this.emittedObject._id);
            if (this.emittedObject.deckName == undefined) {
                const responseFromDecks = await axios.get(url+'/deck/'+this.emittedObject._id);
                this.emittedObject = responseFromDecks.data;
            }
        }
        if (this.emittedObject._id == undefined) {
            this.emittedObject._id = localStorage.getItem("emittedObject._id")
            const responseFromDecks = await axios.get(url+'/deck/'+this.emittedObject._id);
            this.emittedObject = responseFromDecks.data;
        }
        
        if(this.emittedObject.cards.length!=0){
            this.cardPrompt=this.emittedObject.cards[0].cardFront;
            this.cardId=this.emittedObject.cards[0]._id;
        }
        this.deckIsShuffled=false;
    }
}
</script>

<style scoped>
#cardButtonsDiv{
    vertical-align:40%;
}

#singleDeck_css{
    background-color:#325573;
    color:#B6D6F2;
    position:absolute;
    top:0;
    left:0;
    width:100%;
    height:100%;
    min-height:100vh;
    overflow-y:auto; 
}

#welcomeUser {
  /* border:solid black; */
  /* width:fit-content; */
  display:flex;
  justify-content:space-evenly;
  /* justify-content:center; */
  /* align-items:center; */
}

#websiteIcon {
  border:solid black;
}

#welcomeWords {
  /* border:solid black; */
}

#hamburgerMenu {
  border:solid black;
}

.buttonClass {
    color:#14075e;
    background-color:#bfbfc5;
}

#selectBlock{
    border-radius: 8px;
}

.cardButton {
    width: fit-content;
    height: 2em;
    margin: 0em auto;
    font-size: large;
    color:#14075e;
    background-color:#bfbfc5;
    vertical-align: inherit;
}

.cardInputBox {
    width: 17em;
    margin: 0em auto;
    font-size: large;
}

.cardPromptClass1 {
    font-size: x-large;
    color:white;
    display:inline;
}

.cardPromptClass2 {
    font-size: x-large;
}

.card {
    border: 0.1em;
    border-radius: 0.5em;
    padding: 0em 0em 1.5em 0em;
    width: 80%;
    max-width:20em;
    height: 60%;
    margin: auto;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background-color: #517EA6;
    color:black;
}

.flipped {
    background-color:#699EBF;
}

.textBox input{
    font-size: 25px;
    border: 0.1em solid rgb(131, 131, 131);
    border-radius: 0.5em;
    margin: auto;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.addCardButton{
    color:#14075e;
    background-color:#bfbfc5;
}

.editCardButton{
    color:#14075e;
    background-color:#bfbfc5;
}

.deleteCardButton{
    color:#760b0b;;
    background-color:#bfbfc5;
}

.deckDeleteButton{
    color:#760b0b;
    background-color:#bfbfc5;
}

.deckEditButton{
    color:#14075e;
    background-color:#bfbfc5;
}

.decksReturnButton{
    color:#14075e;
    background-color:#bfbfc5;
}

.cardNavigationButtons{
    width: fit-content;
    height: 2em;
    background-color:#bfbfc5;
}

select {
  width: 83%;
  display: block;
  margin: 0 auto;
  font-family: sans-serif;
  font-size: 16px;
  padding: 5px;
}

/* The snackbar - position it at the bottom and in the middle of the screen */
.snackbar {
  visibility: hidden; /* Hidden by default. Visible on click */
  min-width: 250px; /* Set a default minimum width */
  /*margin-left: -125px;*/ /* Divide value of min-width by 2 */
  background-color: #333; /* Black background color */
  color: #fff; /* White text color */
  text-align: center; /* Centered text */
  border-radius: 2px; /* Rounded borders */
  padding: 16px; /* Padding */
  position: fixed; /* Sit on top of the screen */
  z-index: 1; /* Add a z-index if needed */
  /*left: 50%;*/ /* Center the snackbar */
  bottom: 30px; /* 30px from the bottom */
  /* I had to add these next ones to fix the formatting */
  left:3px;
  right:3px;
}

/* Show the snackbar when clicking on a button (class added with JavaScript) */
.snackbar.show {
  visibility: visible; /* Show the snackbar */
  /* Add animation: Take 0.5 seconds to fade in and out the snackbar.
  However, delay the fade out process for 2.5 seconds */
  -webkit-animation: fadein 0.5s, fadeout 0.5s 2.5s;
  animation: fadein 0.5s, fadeout 0.5s 2.5s;
}

/* Animations to fade the snackbar in and out */
@-webkit-keyframes fadein {
  from {bottom: 0; opacity: 0;}
